import { RhinoViewer } from '..';
import { MouseInteraction } from './Mouseinteraction/MouseInteraction';
export class IO {
    app: RhinoViewer;
    mouseInteraction: MouseInteraction;
    constructor(app: RhinoViewer) {
        this.app = app;
        this.mouseInteraction = new MouseInteraction(this.app);
    }
    InitPost() {
        this.mouseInteraction.InitNativeMouseEvent(this.app.divHost);
    }
}