import * as THREE from 'three';

interface JSONMeshData {
    v: number[][];
    f: number[][];
    vc: number[][];
}

export class NMeshWrapperFromJSON {
    private geometry: THREE.BufferGeometry;
    public mesh: THREE.Mesh;

    constructor(json: JSONMeshData) {
        this.geometry = new THREE.BufferGeometry();
        this.initGeometry(json);
        this.initMesh();
    }

    private initGeometry(json: JSONMeshData): void {
        const vertices = new Float32Array(json.v.flat());
        const faces = new Uint32Array(json.f.flat());
        const colors = new Float32Array(json.vc.flat());

        this.geometry.setAttribute('position', new THREE.BufferAttribute(vertices, 3));
        this.geometry.setIndex(new THREE.BufferAttribute(faces, 1));
        this.geometry.setAttribute('color', new THREE.BufferAttribute(colors, 3));

        this.geometry.applyMatrix4(new THREE.Matrix4().makeRotationX(-Math.PI / 2));
        this.geometry.applyMatrix4(new THREE.Matrix4().makeScale(0.3, 0.3, 0.3));

        this.geometry.computeVertexNormals();
        this.geometry.computeBoundingBox();
    }

    private initMesh(): void {
        const material = new THREE.MeshLambertMaterial({ 
            color: 0xff8888,
            vertexColors: true
        });

        this.mesh = new THREE.Mesh(this.geometry, material);
        this.mesh.castShadow = true;
        this.mesh.receiveShadow = true;
    }

    public dispose(scene: THREE.Scene): void {
        scene.remove(this.mesh);
        this.geometry.dispose();
        (this.mesh.material as THREE.Material).dispose();
    }
}